import React, { ChangeEvent, useState } from "react";
import { useNavigate } from "react-router-dom";
import { signIn } from "../../../api/token";
import BottomButton from "../../../common/BottomButton";
import ErrorBar from "../../../common/ErrorBar";
import Modal from "../../../common/Modal";
import TextButton from "../../../common/TextButton";
import UnderlineInput from "../../../common/UnderlineInput";
import { useUserDispatch } from "../../../hooks";

import "./SignIn.scss";

const SignIn = () => {
  const navigate = useNavigate();
  const userDispatch = useUserDispatch();
  const [form, setForm] = useState<{ email: string; password: string }>({
    email: "",
    password: "",
  });
  const [errorMessage, setErrorMessage] = useState<string>("");

  const onChange = (e: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;

    setForm({ ...form, [name]: value });
  };

  return (
    <Modal>
      <TextButton
        className="left sign-in__back-button"
        text="<  "
        onClick={() => {
          navigate("/");
        }}
      />
      <ErrorBar
        className="sign-in__error"
        message={errorMessage}
        icon="warning"
      />
      <UnderlineInput
        placeholder=""
        type="email"
        value={form.email}
        onChange={onChange}
        name="email"
      />
      <UnderlineInput
        placeholder=""
        type="password"
        value={form.password}
        onChange={onChange}
        name="password"
      />
      <div className="sign-in__left">
        <TextButton
          text=" "
          onClick={() => {
            navigate("/findPassword");
          }}
        />
      </div>
      <div className="sign-in__right">
          ?
        <TextButton
          text=" "
          onClick={() => {
            navigate("/signUp");
          }}
        />
      </div>
      <BottomButton
        className="sign-in__submit"
        text=""
        onClick={() => {
          signIn(form)
            .then((res) => {
              if (!res.isSuccess) {
                throw new Error("  .");
              }
              // TODO: accessToken    
              userDispatch({
                type: "SIGN_IN",
                payload: {
                  id: "", // TODO
                  email: form.email,
                  nickname: "", // TODO
                  accessToken: res.accessToken,
                  refreshToken: res.refreshToken,
                },
              });
              navigate("/");
            })
            .catch(() => {
              // TODO:   
              setErrorMessage("/ .");
            });
        }}
      />
    </Modal>
  );
};

export default SignIn;
